<template>
  <div class="orange full-size" @mousemove="mouseMove">
    <div class="mask center" ref="mask">
      <div class="text" v-if="showBackground">
        <p>BUURT ONTDEKKERS</p>
        <p>debuurtontdekkers.nl</p>
        <p>info@debuurtontdekkers.nl</p>
        <p>06-1234 1234</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Contact",
  data() {
    return {
      showBackground: false
    };
  },
  mounted() {
    var vm = this;
    setTimeout(() => {
      vm.showBackground = true;
    }, 500);
  },
  methods: {
    mouseMove(e) {
      var vm = this;
      if (vm.showBackground == false) vm.showBackground = true;
      let mask = this.$refs.mask;
      mask.style.setProperty(["--clip-position"], `${e.pageX}px ${e.pageY}px`);
    }
  }
};
</script>

<style lang="scss" scoped>
.full-size {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #000;
  display: flex;
}
.mask {
  height: 100%;
  width: 100vw;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(#f28c1a, #f28c1a);
  -webkit-clip-path: circle(100px at var(--clip-position));
  clip-path: circle(100px at var(--clip-position));
}
.text {
  height: auto;
  width: 50%;
  display: flex;
  flex-direction: column;
